.detail_arrow {
  position: fixed;
  top: 50%;
  margin-top: -32px;
  font-size: 64px;
  color: #E2E2E2;
  font-family: arial, sans-serif;
  font-weight: bold;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
#prevDetail {
  left: 20%;
}
#nextDetail {
  right: 20%;
}
.image_detail {
  background-color: #1d1d1d;
}
.image_detail a {
  overflow: hidden;
  display: block;
}
.image_detail img {
  max-width: 100%;
  max-height: calc(100vh - 360px);
  display: block;
  margin: 0 auto;
}
.detail_card .card-content {
  position: relative;
}
.detail_card .card-content .content .button {
  vertical-align: middle;
  position: absolute;
  right: 1.5rem;
  font-style: normal !important;
}
.detail_card .card-content .content h1.hover,
.detail_card .card-content .content h2.hover,
.detail_card .card-content .content p.hover {
  text-decoration: underline;
}
.detail_card .card-content .content .subtitle {
  font-style: italic;
}
.spacer {
  padding: 0 .25rem;
}
.filter_links {
  color: #777777;
  text-align: center;
  font-size: 0.75rem;
}
.filter_item {
  white-space: nowrap;
}
