
.img_link {
  cursor: pointer;
}
.img_link:hover {
  opacity: 0.8;
}
.img_link:active {
  opacity: 0.5;
}
.card_container .image .img_link img {
  -o-object-fit: cover;
     object-fit: cover;
  max-width: none;
  width: 100%;
  height: 100%;
}
.card_container .card-image figure {
  width: 200px;
  max-height: 400px;
  overflow: hidden;
  display: block;
}
.card_container .card {
  width: 200px;
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
  margin-bottom: 24px;
}
.card_container .card .card-footer {
  /*display: none;*/
}
.card_container .card:hover .card-footer {
  display: block;
}
.card_container {
  /*display: flex;
  flex-direction: row;
  justify-content: center;*/
  padding-top: 24px;
}
.card_columns {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.card_columns .column {
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
}



